	let couille = [
		{
			name: 'toto'
		}
	]
	let tmpX = 0;

	function startDrag(event) {
		// pics[0].active = false;
		dragging = true;
		tmpX = event.clientX;
		x = event.clientX;
		y = event.clientY;
		// x = this.y = 0;
	};
	function stopDrag() {
		dragging = false;
		x = y = 'no';
	};
	function doDrag(event) {
		if (dragging) {
			x = event.clientX;
			if (x > tmpX + 3 ) {
				swipeLeft();
				tmpX = x;
			} else if (x < tmpX - 3) {
				swipeRight();
				tmpX = x;
			}
		}
	};
	function swipeRight() {
		let index = pics.findIndex(elem => elem.active === true);
		if (index == pics.length - 1) {
			pics[index].active = false;
			index = 0;
			pics[index].active = true;
		}
		pics[index].active = false;
		index++;
		pics[index].active = true;
	}
	function swipeLeft() {
		let index = pics.findIndex(elem => elem.active === true);
		if (index == 0) {
			pics[index].active = false;
			index = pics.length - 1;
			pics[index].active = true;
		}
		pics[index].active = false;
		index--;
		pics[index].active = true;
	}

	window.addEventListener('mouseup', stopDrag);
</script>

<main>
	<h1>Hello you</h1>
	<div class="scroller" on:mousedown={startDrag} on:mousemove={doDrag} on:mouseup(stopDrag)>
		{#each pics as pic}
			<img 
				src={pic.src}
				class='{pic.active ? "active" : "inactive"}'
				alt="couille">
		{/each}
	</div>
</main>

